<template>
    <div>
        <h3 class="mb-5">Настройки</h3>

        <div class="flex flex-wrap -mx-2">

            <div class="w-full lg:w-1/2 xl:w-1/2 mb-4 px-4">

                <vx-card title="WIFI">

                    <div v-if="!$store.state.settings.currentWifi && !$store.state.settings.wifiList.length">
                        <div class="not-bagholders">
                            <div class="icon">
                                <img src="../../assets/images/no_wifi.svg" alt="">
                            </div>

                            <div class="message">
                                Не удалось найти доступные сети WIFI
                            </div>

                            <div class="sub-message">
                                Провеьте точку доступа или используйте подключение по Ethernet
                            </div>

                        </div>
                    </div>

                    <div v-else>
                        <vs-list>
                            <vs-list-header color="black" title="Список доступных сетей"></vs-list-header>

                            <vs-list-item v-if="$store.state.settings.currentWifi"
                                          icon-pack="feather" icon="icon-check"
                                          :title="$store.state.settings.currentWifi.ssid"
                                          :subtitle="$store.state.settings.currentWifi.security">
                                <vs-button size="small" color="danger" type="filled" @click="disconnectWifi">Отключиться
                                </vs-button>
                            </vs-list-item>

                            <vs-list-item v-for="item in $store.state.settings.wifiList" :title="item.ssid"
                                          :subtitle="item.security">
                                <vs-button size="small" color="primary" type="filled" @click="connectWifi(item.ssid)">
                                    Подключиться
                                </vs-button>
                            </vs-list-item>

                        </vs-list>
                    </div>


                </vx-card>
            </div>

            <div class="w-full lg:w-1/2 xl:w-1/2 mb-4 px-4">

                <vx-card class="mb-5" title="Детекция движения">
                    <div class="item">
                        <div class="label">
                            <h5>Датчики движения</h5>
                        </div>
                        <div>
                            <vs-switch color="success" v-model="mdm">
                                <span slot="on">ON</span>
                                <span slot="off">OFF</span>
                            </vs-switch>
                        </div>
                    </div>
                </vx-card>

                <vx-card class="mb-5" title="Световая индикация"
                         subtitle="Выберите цвета подсветки для модулей световой индикации">

                    <div class="flex flex-wrap -mx-2">
                        <div class="w-full md:w-1/2 lg:w-1/2 xl:w-1/2 mb-4 px-4 center">
                            <h5 class="my-3">Базовый цвет</h5>
                            <color-picker style="height: 180px; width: 180px" variant="persistent" v-bind="colorBase"
                                          @input="onColorBase"></color-picker>
                        </div>
                        <div class="w-full md:w-1/2 lg:w-1/2 xl:w-1/2 mb-4 px-4 center">
                            <h5 class="my-3">Цвет ошибки</h5>
                            <color-picker style="height: 180px; width: 180px" variant="persistent" v-bind="colorError"
                                          @input="onColorError"></color-picker>
                        </div>
                    </div>
                </vx-card>

            </div>

        </div>


        <vs-prompt
            title="Подключение к WiFi"
            accept-text="Подключиться"
            cancel-text="Отмена"
            @cancel="cancelWifiPassword"
            @accept="acceptWifiPassword"
            @close="cancelWifiPassword"
            :active.sync="activeWifiPassword">
            <div class="con-exemple-prompt">
                <div class="mb-1">Введите пароль</div>
                <vs-input autofocus class="w-full" v-model="wifiPassword" @keyup.enter="acceptWifiPassword"/>
            </div>
        </vs-prompt>

    </div>
</template>

<script>
    import ColorPicker from '@radial-color-picker/vue-color-picker';
    import debounce from 'lodash/throttle'

    export default {
        components: {ColorPicker},
        computed: {
            colorBase() {
                return {
                    hue: this.$store.state.settings.baseColor,
                    saturation: 100,
                    luminosity: 50,
                    alpha: 1
                }
            },
            colorError() {
                return {
                    hue: this.$store.state.settings.errorColor,
                    saturation: 100,
                    luminosity: 50,
                    alpha: 1
                }
            },
            mdm: {
                get(){
                    return this.$store.state.settings.mdm
                },
                set(val){
                    this.$io.emit('settings:mdm', val);
                }
            }
        },
        data() {
            return {
                activeWifiPassword: false,
                wifiPassword: '',
                currentSsid: '',
            }
        },
        methods: {
            connectWifi(ssid) {
                this.activeWifiPassword = true;
                this.currentSsid = ssid;
            },
            disconnectWifi() {
                this.$vs.dialog({
                    type: 'confirm',
                    color: 'danger',
                    title: `Подтвердите действие`,
                    text: 'Вы собираетесь отлючиться от Wifi сети',
                    acceptText: "Отключиться",
                    cancelText: "Отмена",
                    accept: this.acceptDisconnectWifi
                })
            },
            acceptDisconnectWifi() {
                this.$io.emit('setting:wifi:disconnect');
                console.log('Disconnect')
            },
            acceptWifiPassword() {
                this.$io.emit('setting:wifi:connect', {
                    ssid: this.currentSsid,
                    password: this.wifiPassword
                });
                this.cancelWifiPassword();
            },
            cancelWifiPassword() {
                this.activeWifiPassword = false;
                this.wifiPassword = '';
                this.currentSsid = '';
            },
            onColorBase: debounce(function(hue){
                let rgb = this.hsl2rgb(this.colorBase.hue, this.colorBase.saturation, this.colorBase.luminosity);
                this.$io.emit('settings:color:base', Object.assign(rgb, {hue}));
            }, 150),
            onColorError: debounce(function(hue){
                let rgb = this.hsl2rgb(this.colorError.hue, this.colorError.saturation, this.colorError.luminosity)
                this.$io.emit('settings:color:error', Object.assign(rgb, {hue}))
            }, 150),
            hsl2rgb(h, s, l) {
                s /= 100;
                l /= 100;

                let c = (1 - Math.abs(2 * l - 1)) * s,
                    x = c * (1 - Math.abs((h / 60) % 2 - 1)),
                    m = l - c / 2,
                    r = 0,
                    g = 0,
                    b = 0;

                if (0 <= h && h < 60) {
                    r = c;
                    g = x;
                    b = 0;
                } else if (60 <= h && h < 120) {
                    r = x;
                    g = c;
                    b = 0;
                } else if (120 <= h && h < 180) {
                    r = 0;
                    g = c;
                    b = x;
                } else if (180 <= h && h < 240) {
                    r = 0;
                    g = x;
                    b = c;
                } else if (240 <= h && h < 300) {
                    r = x;
                    g = 0;
                    b = c;
                } else if (300 <= h && h < 360) {
                    r = c;
                    g = 0;
                    b = x;
                }
                r = Math.round((r + m) * 255);
                g = Math.round((g + m) * 255);
                b = Math.round((b + m) * 255);
                return {r, g, b}
            }
        },
    }
</script>

